import { Union } from "./fable_modules/fable-library.4.0.1/Types.js";
import { union_type } from "./fable_modules/fable-library.4.0.1/Reflection.js";
import { ofNullable, map, defaultArg } from "./fable_modules/fable-library.4.0.1/Option.js";
import { createElement } from "react";
import React from "react";
import { createObj } from "./fable_modules/fable-library.4.0.1/Util.js";
import { Interop_reactApi } from "./fable_modules/Feliz.2.6.0/./Interop.fs.js";
import { ofArray } from "./fable_modules/fable-library.4.0.1/List.js";

export class ThemeType extends Union {
    "constructor"(tag, fields) {
        super();
        this.tag = tag;
        this.fields = fields;
    }
    cases() {
        return ["Light", "Dark", "Cyberpunk", "Synthwave", "Retro"];
    }
}

export function ThemeType$reflection() {
    return union_type("App.Theme.ThemeType", [], ThemeType, () => [[], [], [], [], []]);
}

export function fromString(_arg) {
    if (_arg === "light") {
        return new ThemeType(0, []);
    }
    else if (_arg === "dark") {
        return new ThemeType(1, []);
    }
    else if (_arg === "cyberpunk") {
        return new ThemeType(2, []);
    }
    else if (_arg === "synthwave") {
        return new ThemeType(3, []);
    }
    else if (_arg === "retro") {
        return new ThemeType(4, []);
    }
    else {
        return new ThemeType(0, []);
    }
}

export function toString(_arg) {
    switch (_arg.tag) {
        case 1: {
            return "dark";
        }
        case 2: {
            return "cyberpunk";
        }
        case 3: {
            return "synthwave";
        }
        case 4: {
            return "retro";
        }
        default: {
            return "light";
        }
    }
}

export const themeKey = "luisfx-theme";

export const defaultTheme = new ThemeType(0, []);

export function getStoredTheme() {
    return defaultArg(map(fromString, ofNullable(localStorage.getItem(themeKey))), defaultTheme);
}

export function setTheme(theme) {
    const themeStr = toString(theme);
    localStorage.setItem(themeKey, themeStr);
    document.documentElement.setAttribute("data-theme", themeStr);
}

export function isDarkTheme(theme) {
    switch (theme.tag) {
        case 1:
        case 2:
        case 3: {
            return true;
        }
        default: {
            return false;
        }
    }
}

export function toggleTheme(currentTheme) {
    switch (currentTheme.tag) {
        case 1: {
            return new ThemeType(0, []);
        }
        case 2: {
            return new ThemeType(3, []);
        }
        case 3: {
            return new ThemeType(4, []);
        }
        case 4: {
            return new ThemeType(0, []);
        }
        default: {
            return new ThemeType(1, []);
        }
    }
}

export function getThemeIcon(_arg) {
    switch (_arg.tag) {
        case 1: {
            return "🌙";
        }
        case 2: {
            return "🤖";
        }
        case 3: {
            return "🌆";
        }
        case 4: {
            return "👾";
        }
        default: {
            return "☀️";
        }
    }
}

export function ThemeToggle(themeToggleInputProps) {
    let elems;
    const onThemeChange = themeToggleInputProps.onThemeChange;
    const currentTheme = themeToggleInputProps.currentTheme;
    return createElement("button", createObj(ofArray([["className", "btn btn-ghost btn-circle"], ["aria-label", "Toggle theme"], ["onClick", (_arg) => {
        onThemeChange(toggleTheme(currentTheme));
    }], (elems = [createElement("span", {
        className: "text-xl",
        children: getThemeIcon(currentTheme),
    })], ["children", Interop_reactApi.Children.toArray(Array.from(elems))])])));
}

export function initializeTheme() {
    const storedTheme = getStoredTheme();
    setTheme(storedTheme);
    return storedTheme;
}

