import { Record, Union } from "./fable_modules/fable-library.4.0.1/Types.js";
import { record_type, float64_type, bool_type, union_type } from "./fable_modules/fable-library.4.0.1/Reflection.js";
import { empty, singleton, tail, head, isEmpty } from "./fable_modules/fable-library.4.0.1/List.js";
import { setTheme, initializeTheme, ThemeType$reflection } from "./Theme.fs.js";
import { Cmd_none } from "./fable_modules/Fable.Elmish.4.3.0/cmd.fs.js";

export class Page extends Union {
    "constructor"(tag, fields) {
        super();
        this.tag = tag;
        this.fields = fields;
    }
    cases() {
        return ["Home", "About", "Experience", "Projects", "Skills", "Blog", "Contact"];
    }
}

export function Page$reflection() {
    return union_type("App.Page", [], Page, () => [[], [], [], [], [], [], []]);
}

export function State_fromUrl(_arg) {
    let matchResult;
    if (!isEmpty(_arg)) {
        if (head(_arg) === "about") {
            if (isEmpty(tail(_arg))) {
                matchResult = 1;
            }
            else {
                matchResult = 7;
            }
        }
        else if (head(_arg) === "experience") {
            if (isEmpty(tail(_arg))) {
                matchResult = 2;
            }
            else {
                matchResult = 7;
            }
        }
        else if (head(_arg) === "projects") {
            if (isEmpty(tail(_arg))) {
                matchResult = 3;
            }
            else {
                matchResult = 7;
            }
        }
        else if (head(_arg) === "skills") {
            if (isEmpty(tail(_arg))) {
                matchResult = 4;
            }
            else {
                matchResult = 7;
            }
        }
        else if (head(_arg) === "blog") {
            if (isEmpty(tail(_arg))) {
                matchResult = 5;
            }
            else {
                matchResult = 7;
            }
        }
        else if (head(_arg) === "contact") {
            if (isEmpty(tail(_arg))) {
                matchResult = 6;
            }
            else {
                matchResult = 7;
            }
        }
        else {
            matchResult = 7;
        }
    }
    else {
        matchResult = 0;
    }
    switch (matchResult) {
        case 0: {
            return new Page(0, []);
        }
        case 1: {
            return new Page(1, []);
        }
        case 2: {
            return new Page(2, []);
        }
        case 3: {
            return new Page(3, []);
        }
        case 4: {
            return new Page(4, []);
        }
        case 5: {
            return new Page(5, []);
        }
        case 6: {
            return new Page(6, []);
        }
        case 7: {
            return new Page(0, []);
        }
    }
}

export function State_toUrl(_arg) {
    switch (_arg.tag) {
        case 1: {
            return singleton("about");
        }
        case 2: {
            return singleton("experience");
        }
        case 3: {
            return singleton("projects");
        }
        case 4: {
            return singleton("skills");
        }
        case 5: {
            return singleton("blog");
        }
        case 6: {
            return singleton("contact");
        }
        default: {
            return empty();
        }
    }
}

export function State_toTitle(_arg) {
    switch (_arg.tag) {
        case 1: {
            return "About";
        }
        case 2: {
            return "Experience";
        }
        case 3: {
            return "Projects";
        }
        case 4: {
            return "Skills";
        }
        case 5: {
            return "Blog";
        }
        case 6: {
            return "Contact";
        }
        default: {
            return "Home";
        }
    }
}

export class State_Model extends Record {
    "constructor"(CurrentPage, CurrentTheme, IsMenuOpen, ScrollPosition) {
        super();
        this.CurrentPage = CurrentPage;
        this.CurrentTheme = CurrentTheme;
        this.IsMenuOpen = IsMenuOpen;
        this.ScrollPosition = ScrollPosition;
    }
}

export function State_Model$reflection() {
    return record_type("App.State.Model", [], State_Model, () => [["CurrentPage", Page$reflection()], ["CurrentTheme", ThemeType$reflection()], ["IsMenuOpen", bool_type], ["ScrollPosition", float64_type]]);
}

export class State_Msg extends Union {
    "constructor"(tag, fields) {
        super();
        this.tag = tag;
        this.fields = fields;
    }
    cases() {
        return ["NavigateTo", "SetTheme", "ToggleMenu", "UpdateScrollPosition"];
    }
}

export function State_Msg$reflection() {
    return union_type("App.State.Msg", [], State_Msg, () => [[["Item", Page$reflection()]], [["Item", ThemeType$reflection()]], [], [["Item", float64_type]]]);
}

export function State_init() {
    return [new State_Model(new Page(0, []), initializeTheme(), false, 0), Cmd_none()];
}

export function State_update(msg, model) {
    switch (msg.tag) {
        case 1: {
            const theme = msg.fields[0];
            setTheme(theme);
            return [new State_Model(model.CurrentPage, theme, model.IsMenuOpen, model.ScrollPosition), Cmd_none()];
        }
        case 2: {
            return [new State_Model(model.CurrentPage, model.CurrentTheme, !model.IsMenuOpen, model.ScrollPosition), Cmd_none()];
        }
        case 3: {
            return [new State_Model(model.CurrentPage, model.CurrentTheme, model.IsMenuOpen, msg.fields[0]), Cmd_none()];
        }
        default: {
            window.scrollTo(0, 0);
            return [new State_Model(msg.fields[0], model.CurrentTheme, false, model.ScrollPosition), Cmd_none()];
        }
    }
}

